'use client';

import React, { createContext, useState, useContext, ReactNode, useEffect, useCallback, useRef } from 'react';

//   
interface ThemeContextType {
  theme: 'light' | 'dark';
  toggleTheme: () => void;
  setDarkMode: (enabled: boolean) => void;
  isDarkMode: boolean;
}

//   
interface AppSettings {
  darkMode?: boolean;
  [key: string]: any;
}

const ThemeContext = createContext<ThemeContextType>({
  theme: 'light',
  toggleTheme: () => {},
  setDarkMode: () => {},
  isDarkMode: false,
});

// useTheme  
export const useTheme = () => useContext(ThemeContext);

interface ThemeProviderProps {
  children: ReactNode;
}

//     
const safeLocalStorage = {
  getItem: (key: string): string | null => {
    try {
      return localStorage.getItem(key);
    } catch (e) {
      console.error('   :', e);
      return null;
    }
  },
  
  setItem: (key: string, value: string): boolean => {
    try {
      localStorage.setItem(key, value);
      return true;
    } catch (e) {
      console.error('   :', e);
      return false;
    }
  }
};

//   
const getAppSettings = (): AppSettings => {
  const savedSettings = safeLocalStorage.getItem('app-settings');
  if (!savedSettings) return {};
  
  try {
    return JSON.parse(savedSettings);
  } catch (e) {
    console.error('  :', e);
    return {};
  }
};

//   
const saveAppSettings = (settings: AppSettings): boolean => {
  try {
    const currentSettings = getAppSettings();
    const newSettings = { ...currentSettings, ...settings };
    return safeLocalStorage.setItem('app-settings', JSON.stringify(newSettings));
  } catch (e) {
    console.error('  :', e);
    return false;
  }
};

// CSS   
const applyCSSVariables = (isDarkMode: boolean) => {
  const root = document.documentElement;
  
  if (isDarkMode) {
    root.style.setProperty('--card-bg', '#222222');
    root.style.setProperty('--text-color', 'rgba(255, 255, 255, 0.87)');
    root.style.setProperty('--border-color', 'rgba(255, 255, 255, 0.12)');
    root.style.setProperty('--button-hover-bg', 'rgba(255, 255, 255, 0.08)');
    root.style.setProperty('--primary-color', '#90caf9');
  } else {
    root.style.setProperty('--card-bg', '#ffffff');
    root.style.setProperty('--text-color', 'rgba(0, 0, 0, 0.87)');
    root.style.setProperty('--border-color', 'rgba(0, 0, 0, 0.12)');
    root.style.setProperty('--button-hover-bg', 'rgba(0, 0, 0, 0.05)');
    root.style.setProperty('--primary-color', '#1976d2');
  }
};

export function ThemeProvider({ children }: ThemeProviderProps) {
  const [theme, setTheme] = useState<'light' | 'dark'>('light');
  const [isDarkMode, setIsDarkMode] = useState<boolean>(false);
  const [isInitialized, setIsInitialized] = useState<boolean>(false);
  const themeChangeRef = useRef<boolean>(false);
  
  //   CSS  
  const applyDarkModeCSS = useCallback((enabled: boolean) => {
    if (enabled) {
      document.documentElement.classList.add('dark-mode');
      document.body.classList.add('dark-mode');
    } else {
      document.documentElement.classList.remove('dark-mode');
      document.body.classList.remove('dark-mode');
    }
    
    // CSS  
    applyCSSVariables(enabled);
  }, []);
  
  //    
  const setDarkMode = useCallback((enabled: boolean) => {
    //   
    if (themeChangeRef.current) return;
    themeChangeRef.current = true;
    
    setTheme(enabled ? 'dark' : 'light');
    setIsDarkMode(enabled);
    
    // HTML  dark-mode  
    applyDarkModeCSS(enabled);
    
    //    
    saveAppSettings({ darkMode: enabled });
    
    // Electron API   
    if (window.electronAPI && window.electronAPI.setDarkMode) {
      window.electronAPI.setDarkMode(enabled)
        .catch((error: any) => {
          console.error('Electron API     :', error);
        });
    }
    
    // 500ms    ()
    setTimeout(() => {
      themeChangeRef.current = false;
    }, 500);
  }, [applyDarkModeCSS]);
  
  //   
  const toggleTheme = useCallback(() => {
    if (!themeChangeRef.current) {
      setDarkMode(!isDarkMode);
    }
  }, [isDarkMode, setDarkMode]);
  
  //      
  useEffect(() => {
    //   
    if (isInitialized) return;
    
    try {
      //    :
      // 1.    (   )
      // 2. Electron API  (  )
      // 3.     ()
      
      //    
      const settings = getAppSettings();
      const hasSavedSettings = settings.darkMode !== undefined;
      
      //    (  )
      if (hasSavedSettings) {
        const savedMode = settings.darkMode === true;
        applyDarkModeCSS(savedMode);
        setTheme(savedMode ? 'dark' : 'light');
        setIsDarkMode(savedMode);
      } else {
        //   
        const darkModeMediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
        const systemDarkMode = darkModeMediaQuery.matches;
        applyDarkModeCSS(systemDarkMode);
        setTheme(systemDarkMode ? 'dark' : 'light');
        setIsDarkMode(systemDarkMode);
      }
      
      // Electron API  ()
      const loadElectronSettings = async () => {
        if (window.electronAPI && window.electronAPI.getDarkMode) {
          try {
            const darkModeEnabled = await window.electronAPI.getDarkMode();
            //    (  )
            if (!themeChangeRef.current && (!hasSavedSettings || settings.darkMode !== darkModeEnabled)) {
              themeChangeRef.current = true;
              setDarkMode(darkModeEnabled);
              setTimeout(() => {
                themeChangeRef.current = false;
              }, 500);
            }
          } catch (error) {
            console.error('Electron API      :', error);
          }
        }
        
        //   
        setIsInitialized(true);
      };
      
      loadElectronSettings();
      
      //     
      const darkModeMediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
      
      const handleColorSchemeChange = (e: MediaQueryListEvent) => {
        //       
        try {
          const settings = getAppSettings();
          if (settings.darkMode === undefined && !themeChangeRef.current) {
            setDarkMode(e.matches);
          }
        } catch (error) {
          console.error('    :', error);
        }
      };
      
      darkModeMediaQuery.addEventListener('change', handleColorSchemeChange);
      
      return () => {
        darkModeMediaQuery.removeEventListener('change', handleColorSchemeChange);
      };
    } catch (error) {
      console.error('   :', error);
      //     
      setIsInitialized(true);
    }
  }, [applyDarkModeCSS, setDarkMode, isInitialized]);

  //       (       )
  useEffect(() => {
    //      
    const handleBeforeUnload = () => {
      try {
        localStorage.setItem('theme-last-state', isDarkMode ? 'dark' : 'light');
      } catch (e) {
        console.error('localStorage  :', e);
      }
    };

    window.addEventListener('beforeunload', handleBeforeUnload);
    
    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
    };
  }, [isDarkMode]);

  return (
    <ThemeContext.Provider value={{ theme, toggleTheme, setDarkMode, isDarkMode }}>
      {children}
    </ThemeContext.Provider>
  );
}