import type { 
  MemoryInfo, 
  OptimizationResult, 
  GCResult, 
  GpuInfo,
  GpuComputationResult,
  TaskResult
} from '@/types';
import { OptimizationLevel } from '@/types/native-module';

//  
let moduleStatusCache: any = null;
let lastStatusCheck = 0;
const STATUS_CACHE_TTL = 10000; // 10

//    -  
const isBrowser = typeof window !== 'undefined';

/**
 * fetch   
 */
async function enhancedFetch(url: string, options: RequestInit = {}): Promise<Response> {
  try {
    const response = await fetch(url, {
      ...options,
      headers: {
        'Content-Type': 'application/json',
        ...options.headers,
      },
    });
    
    return response;
  } catch (error) {
    console.error(`Fetch   (${url}):`, error);
    throw error;
  }
}

/**
 *   
 */
export async function getMemoryInfo() {
  if (!isBrowser) {
    return { success: false, error: 'Server environment', timestamp: Date.now() };
  }
  
  try {
    const response = await enhancedFetch('/api/native/memory');
    
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    
    return await response.json();
  } catch (error) {
    console.error('   :', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : '   ',
    };
  }
}

/**
 *   
 */
export async function optimizeMemory(level = 2, emergency = false) {
  try {
    const response = await enhancedFetch('/api/native/memory', {
      method: 'PUT',
      body: JSON.stringify({
        type: 'optimize',
        level: level.toString(),
        emergency
      }),
    });
    
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    
    return await response.json();
  } catch (error) {
    console.error('  :', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : '   ',
    };
  }
}

/**
 *    
 */
export async function forceGarbageCollection() {
  try {
    //   
    const data = JSON.stringify({
      type: 'gc'
    });
    
    //  POST  
    try {
      const response = await enhancedFetch('/api/native/memory', {
        method: 'POST',
        body: data,
      });
      
      if (response.ok) {
        return await response.json();
      }
      
      // POST  PUT  
      console.warn('POST  , PUT   .');
    } catch (error) {
      console.warn('POST  , PUT   .', error);
    }
    
    // PUT  
    const response = await enhancedFetch('/api/native/memory', {
      method: 'PUT',
      body: data,
    });
    
    if (!response.ok) {
      //    
      console.error(`   : ${response.status} ${response.statusText}`);
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    
    return await response.json();
  } catch (error) {
    console.error('  :', error);
    
    //         
    try {
      if (typeof window !== 'undefined' && typeof window.gc === 'function') {
        window.gc();
        console.log(' GC  ');
        return {
          success: true,
          message: ' GC  ',
          timestamp: Date.now()
        };
      }
    } catch (fallbackError) {
      console.warn(' GC  :', fallbackError);
    }
    
    return {
      success: false,
      error: error instanceof Error ? error.message : '   ',
      timestamp: Date.now()
    };
  }
}

/**
 * GPU  
 */
export async function getGpuInfo() {
  try {
    //   
    if (!isBrowser) {
      return createFallbackGpuInfo('   ');
    }
    
    //    
    if (!window.navigator) {
      return createFallbackGpuInfo('navigator    ');
    }
    
    try {
      const response = await enhancedFetch('/api/native/gpu');
    
    if (!response.ok) {
        console.warn(`GPU   : ${response.status}.  GPU  `);
        return createFallbackGpuInfo(`API  : ${response.status}`);
      }
      
      const result = await response.json();
      
      //  
      if (result && result.success) {
        return result;
      } else {
        console.warn('GPU      .   ', result);
        return createFallbackGpuInfo('   ');
      }
    } catch (fetchError) {
      console.error('GPU    :', fetchError);
      return createFallbackGpuInfo(fetchError instanceof Error ? fetchError.message : ' ');
    }
  } catch (error) {
    console.error('GPU       :', error);
    return createFallbackGpuInfo('  ');
  }
}

/**
 *  GPU  
 */
function createFallbackGpuInfo(reason: string) {
  //  WebGL   
  let webglAvailable = false;
  let vendorInfo = '  ';
  
  try {
    if (typeof window !== 'undefined') {
      const canvas = document.createElement('canvas');
      const gl = canvas.getContext('webgl') || canvas.getContext('experimental-webgl') as WebGLRenderingContext | null;
      
      if (gl) {
        webglAvailable = true;
        const debugInfo = gl.getExtension('WEBGL_debug_renderer_info');
        
        if (debugInfo) {
          const vendor = gl.getParameter(debugInfo.UNMASKED_VENDOR_WEBGL) || '  ';
          const renderer = gl.getParameter(debugInfo.UNMASKED_RENDERER_WEBGL) || '  ';
          vendorInfo = `${vendor} (${renderer})`;
        }
      }
    }
  } catch (e) {
    console.warn('WebGL    :', e);
  }
  
  return {
    success: true, //        success:true 
    available: webglAvailable,
    fallback: true, //    
    gpuInfo: {
      name: ' ',
      vendor: vendorInfo,
      available: webglAvailable,
      accelerationEnabled: false,
      deviceType: 'Software',
      driverVersion: ' ',
      fallbackReason: reason
    },
      timestamp: Date.now()
    };
}

/**
 * GPU  /
 * @param enable  
 */
export async function setGpuAcceleration(enable: boolean) {
  try {
    //   
    const requestBody = JSON.stringify({
      enable
    });
    
    //  
    const response = await enhancedFetch('/api/native/gpu/acceleration', {
      method: 'PUT',
      body: requestBody
    });
    
    //  
    if (!response.ok) {
      throw new Error(`GPU    : ${response.status}`);
    }
    
    return await response.json();
  } catch (error) {
    console.error('GPU   :', error);
    return {
      success: false,
      enabled: false,
      result: false,
      error: error instanceof Error ? error.message : '   ',
      timestamp: Date.now()
    };
  }
}

